import {
  Component,
  Input,
  TemplateRef,
  ContentChild,
  OnInit,
  OnChanges,
  Inject,
  Optional,
  ViewChild,
  ElementRef,
  AfterViewInit,
  Renderer2,
  OnDestroy,
} from '@angular/core';
import { Router } from '@angular/router';
import { NzAffixComponent } from 'ng-zorro-antd';
import { Subscription } from 'rxjs';
import { filter } from 'rxjs/operators';

import { isEmpty, InputBoolean, InputNumber } from '@delon/util';
import {
  MenuService,
  ALAIN_I18N_TOKEN,
  AlainI18NService,
  Menu,
  TitleService,
  SettingsService,
} from '@delon/theme';
import { ReuseTabService } from '@delon/abc/reuse-tab';

import { PageHeaderConfig } from './page-header.config';

@Component({
  selector: 'page-header',
  templateUrl: './page-header.component.html',
  preserveWhitespaces: false,
})
export class PageHeaderComponent
  implements OnInit, OnChanges, AfterViewInit, OnDestroy {
  private inited = false;
  private i18n$: Subscription;
  private set$: Subscription;
  @ViewChild('conTpl')
  private conTpl: ElementRef;
  @ViewChild('affix')
  private affix: NzAffixComponent;
  private _menus: Menu[];

  private get menus() {
    if (this._menus) {
      return this._menus;
    }
    this._menus = this.menuSrv.getPathByUrl(this.route.url.split('?')[0], this.recursiveBreadcrumb);

    return this._menus;
  }

  // #region fields

  _title: string;
  _titleTpl: TemplateRef<any>;
  @Input()
  set title(value: string | TemplateRef<any>) {
    if (value instanceof TemplateRef) {
      this._title = null;
      this._titleTpl = value;
    } else {
      this._title = value;
    }
  }

  @Input()
  @InputBoolean()
  loading = false;

  @Input()
  @InputBoolean()
  wide = false;

  @Input()
  home: string;

  @Input()
  homeLink: string;

  @Input()
  homeI18n: string;

  /**
   * èªå¨çæå¯¼èªï¼ä»¥å½åè·¯ç±ä»ä¸»èåä¸­å®ä½
   */
  @Input()
  @InputBoolean()
  autoBreadcrumb: boolean;

  /**
   * èªå¨çææ é¢ï¼ä»¥å½åè·¯ç±ä»ä¸»èåä¸­å®ä½
   */
  @Input()
  @InputBoolean()
  autoTitle: boolean;

  /**
   * æ¯å¦èªå¨å°æ é¢åæ­¥è³ `TitleService`ã`ReuseService` ä¸ï¼ä» `title` ä¸º `string` ç±»åæ¶ææ
   */
  @Input()
  @InputBoolean()
  syncTitle: boolean;

  @Input()
  @InputBoolean()
  fixed: boolean;

  @Input()
  @InputNumber()
  fixedOffsetTop: number;

  paths: any[] = [];

  @Input()
  breadcrumb: TemplateRef<any>;

  @Input()
  @InputBoolean()
  recursiveBreadcrumb: boolean;

  @Input()
  logo: TemplateRef<any>;

  @Input()
  action: TemplateRef<any>;

  @Input()
  content: TemplateRef<any>;

  @Input()
  extra: TemplateRef<any>;

  @Input()
  tab: TemplateRef<any>;

  // #endregion

  constructor(
    cog: PageHeaderConfig,
    settings: SettingsService,
    private renderer: Renderer2,
    private route: Router,
    private menuSrv: MenuService,
    @Optional()
    @Inject(ALAIN_I18N_TOKEN)
    private i18nSrv: AlainI18NService,
    @Optional()
    @Inject(TitleService)
    private titleSrv: TitleService,
    @Optional()
    @Inject(ReuseTabService)
    private reuseSrv: ReuseTabService,
  ) {
    Object.assign(this, cog);
    if (this.i18nSrv) {
      this.i18n$ = this.i18nSrv.change.subscribe(() => this.refresh());
    }
    this.set$ = settings.notify
      .pipe(
        filter(
          w => this.affix && w.type === 'layout' && w.name === 'collapsed',
        ),
      )
      .subscribe(() => this.affix.updatePosition({}));
  }

  refresh() {
    this.setTitle().genBreadcrumb();
  }

  private genBreadcrumb() {
    if (this.breadcrumb || !this.autoBreadcrumb || this.menus.length <= 0)
      return;
    const paths: any[] = [];
    this.menus.forEach(item => {
      if (typeof item.hideInBreadcrumb !== 'undefined' && item.hideInBreadcrumb)
        return;
      let title = item.text;
      if (item.i18n && this.i18nSrv) title = this.i18nSrv.fanyi(item.i18n);
      paths.push({ title, link: item.link && [item.link] });
    });
    // add home
    if (this.home) {
      paths.splice(0, 0, {
        title:
          (this.homeI18n &&
            this.i18nSrv &&
            this.i18nSrv.fanyi(this.homeI18n)) ||
          this.home,
        link: [this.homeLink],
      });
    }
    this.paths = paths;
    return this;
  }

  private setTitle() {
    if (
      typeof this._title === 'undefined' &&
      typeof this._titleTpl === 'undefined' &&
      this.autoTitle &&
      this.menus.length > 0
    ) {
      const item = this.menus[this.menus.length - 1];
      let title = item.text;
      if (item.i18n && this.i18nSrv) title = this.i18nSrv.fanyi(item.i18n);
      this._title = title;
    }

    if (this._title && this.syncTitle) {
      if (this.titleSrv) {
        this.titleSrv.setTitle(this._title);
      }
      if (this.reuseSrv) {
        this.reuseSrv.title = this._title;
      }
    }

    return this;
  }

  checkContent() {
    if (isEmpty(this.conTpl.nativeElement)) {
      this.renderer.setAttribute(this.conTpl.nativeElement, 'hidden', '');
    } else {
      this.renderer.removeAttribute(this.conTpl.nativeElement, 'hidden');
    }
  }

  ngOnInit() {
    this.refresh();
    this.inited = true;
  }

  ngAfterViewInit(): void {
    this.checkContent();
  }

  ngOnChanges(): void {
    if (this.inited) this.refresh();
  }

  ngOnDestroy(): void {
    if (this.i18n$) this.i18n$.unsubscribe();
    this.set$.unsubscribe();
  }
}
